import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Button } from '@vkontakte/vkui';

class Scanner extends React.Component {
   
	constructor(){
        super()
        
    }
    componentDidMount(){
        bridge.subscribe(this.onBridgeResult);
    }
    onBridgeResult = (e) => {
        console.log("bridge e = " + JSON.stringify(e))
        switch (e.detail.type) {
            case "VKWebAppOpenCodeReaderResult":
                this.setState({qrResult : e.detail.data})
            break;
            case "VKWebAppOpenCodeReaderFailed":

            break;
      
            default:
            break;
        }
    }
    openScanner = () => {
        bridge.send("VKWebAppOpenCodeReader")
        // .then(data => { 
        //         this.setState({flashDetails : ""})
        // }) 
        // .catch(error => { 
        //     this.setState({flashDetails : "Нет доступа к Камере"})
        // });
    }
	render() {
	  return (
		<div>
            <Button onClick={this.openScanner} size="m">Medium</Button>
            {this.state.qrResult && <p>{JSON.stringify(this.state.qrResult)}</p>}
        </div>
	  )
	}
  }
  export default Scanner;